import React from "react";
import { AppBar, Toolbar, Box } from "@material-ui/core";
import { NavLink } from "react-router-dom";

import "./nav.css";
const NavBar = () => {
  return (
    <Box>
      <AppBar position="static">
        <Toolbar className="nav-tool">
          <NavLink to="./" exact className="btn-tool">
            To-Do List
          </NavLink>
          <NavLink to="./adduser" exact className="btn-tool">
            Add User
          </NavLink>
          <NavLink to="./allusers" exact button className="btn-tool">
            All Users
          </NavLink>
        </Toolbar>
      </AppBar>
    </Box>
  );
};

export default NavBar;
